@import 'sg.less';
@white: rgb(255, 253, 249);
@black: #000;
@logo_color: #cd5c5c;
@light_grey: rgb(209, 209, 209);
@dark_grey: rgba(192, 192, 192, 0.8);
.reset();
.lobster() {
    font-family: 'Lobster', cursive;
}

.lato() {
    font-family: 'Lato', sans-serif;
}

.raleway() {
    font-family: 'Raleway', sans-serif;
}

.rubik() {
    font-family: 'Rubik', sans-serif;
}

@font-face {
    font-family: 'Lobster', cursive;
    font-family: 'Rubik', sans-serif;
    font-family: 'Lato', sans-serif;
    font-family: 'Raleway', sans-serif;
    src: url('https://fonts.googleapis.com/css?family=Lobster|Rubik:400,700,900&subset=cyrillic|Lato:300,300i,400,400i,700,700i|Raleway:300,300i,400,400i,500,500i,700,700i');
}

.body {
    background: @black;
    font-size: 16px;
    .lato();
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.background-fixed {
    background: url(../img/bg_img_2.jpg) no-repeat top center/cover;
    position: fixed;
    width: 100%;
    height: 100vh;
    z-index: -9999;
}

.wrapper {
    .wrapper();
}

.header {
    background: linear-gradient(rgba(0, 0, 0, 1) 50px, rgba(0, 0, 0, 0.8) 150px);
    // background: rgba(1e, 1e, 1e, 0.8);
    padding-top: 20px;
    padding-bottom: 50px;
}

.header-top {
    .row-flex();
    .col();
    .md-block( {
        flex-direction: column;
        align-items: center;
    }
    );
    &__logo {
        color: @logo_color;
        cursor: pointer;
        font-size: 2.5em;
        .lobster();
        .col();
        .md-block( {
            padding-top: 25px;
        }
        );
    }
    &__nav {
        .col();
        display: flex;
        margin-left: auto;
        .md-block( {
            margin: 50px auto 0 auto;
        }
        );
    }
}

.logo {
    &__title {
        opacity: 1;
    }
}

.nav {
    &__ul {
        display: flex;
        .xs-block( {
            flex-direction: column;
        }
        );
    }
    &__li {
        cursor: pointer;
        display: flex;
    }
    &__li:hover {
        color: @white;
    }
    &_font-size_m {
        font-size: 1.5rem;
        margin: 0 20px;
    }
    &__theme_light {
        color: @light_grey;
    }
    &__link {
        color: inherit;
        text-decoration: none;
        align-self: center;
    }
}

.braces {
    color: @light_grey;
}

.main-title {
    color: @light_grey;
    font-size: 1.5rem;
    margin-top: 100px;
    line-height: 1.5;
    padding: 0 15px;
    .row-flex();
    .col();
    justify-content: space-around;
    .md-block( {
        margin-top: 50px;
        flex-direction: column;
        align-items: center;
    }
    );
    &__text {
        &_bold {
            font-weight: 400;
        }
        .col();
        .size(5);
        .size-md(12);
        .md-block( {
            margin: 25px 0;
        }
        );
    }
    &__slider {
        .col();
        .size(5);
        .size-md(12);
        .md-block( {
            margin-bottom: 25px;
        }
        );
        position: relative;
        width: 500px;
    }
    &__auto-height {
        padding-top: 50%;
        height: 0;
    }
}

.frame-grey {
    border: rgb(168, 168, 168) 3px solid;
    border-radius: 5px;
}

.block-message {
    border-radius: 25px;
    border: rgb(168, 168, 168) 3px solid;
    background: rgba(255, 255, 255, 0.9);
}

.rectangle-slider {
    width: 100%;
    height: 100%;
    background: @white;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    &__img {
        opacity: 0;
        position: absolute;
    }
    &__img:first-child {
        opacity: 1;
    }
}

.portfolio {
    background: @white;
    padding: 45px 0;
    &__items {
        .row-flex();
        .col();
        padding-top: 45px;
        justify-content: center;
    }
    &__item {
        .col();
        .size(5);
        .size-xs(12);
        margin-bottom: 15px;
    }
    &__link {
        display: block;
    }
}

.portfolio-title {
    color: @logo_color;
}

.title {
    font-size: 1.8em;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
}

.contacts {
    color: @white;
}

.contacts-title {
    font-size: 1.5em;
    padding-bottom: 10px;
}

.footer {
    margin-top: auto;
    background: @logo_color;
    padding: 40px 0;
    color: @white;
    &__flex {
        flex-basis: 100%;
        display: flex;
        .md-block( {
            flex-direction: column;
            align-items: center;
        });
    }
    &__nav {
        .col();
        display: flex;
        margin-left: auto;
        .md-block( {
            margin: 50px auto 0 auto;
        }
        );
    }
}
.email {
    font-weight: 600;
    &__link {
        color: rgb(0, 255, 115);
        text-decoration: none;
        font-weight: 400;
    }
}
.btn-up {
    width: 50px;
    position:fixed;
    top: 90%;
    opacity: 0.8;
    display: none;
    cursor: pointer;
}
