body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

@font-face {
  font-family: 'Lobster', cursive;
  font-family: 'Rubik', sans-serif;
  font-family: 'Lato', sans-serif;
  font-family: 'Raleway', sans-serif;
  src: url('https://fonts.googleapis.com/css?family=Lobster|Rubik:400,700,900&subset=cyrillic|Lato:300,300i,400,400i,700,700i|Raleway:300,300i,400,400i,500,500i,700,700i');
}

.body {
  background: #000;
  font-size: 16px;
  font-family: 'Lato', sans-serif;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.background-fixed {
  background: url(../img/bg_img_2.jpg) no-repeat top center / cover;
  position: fixed;
  width: 100%;
  height: 100vh;
  z-index: -9999;
}

.wrapper {
  max-width: 1070px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.header {
  background: linear-gradient(#000000 50px, rgba(0, 0, 0, 0.8) 150px);
  padding-top: 20px;
  padding-bottom: 50px;
}

.header-top {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
}

.header-top__logo {
  color: #cd5c5c;
  cursor: pointer;
  font-size: 2.5em;
  font-family: 'Lobster', cursive;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
}

.header-top__nav {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  display: flex;
  margin-left: auto;
}

.logo__title {
  opacity: 1;
}

.nav__ul {
  display: flex;
}

.nav__li {
  cursor: pointer;
  display: flex;
}

.nav__li:hover {
  color: #fffdf9;
}

.nav_font-size_m {
  font-size: 1.5rem;
  margin: 0 20px;
}

.nav__theme_light {
  color: #d1d1d1;
}

.nav__link {
  color: inherit;
  text-decoration: none;
  align-self: center;
}

.braces {
  color: #d1d1d1;
}

.main-title {
  color: #d1d1d1;
  font-size: 1.5rem;
  margin-top: 100px;
  line-height: 1.5;
  padding: 0 15px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  justify-content: space-around;
}

.main-title__text {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(41.66666666666667% - 30px);
}

.main-title__text_bold {
  font-weight: 400;
}

.main-title__slider {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(41.66666666666667% - 30px);
  position: relative;
  width: 500px;
}

.main-title__auto-height {
  padding-top: 50%;
  height: 0;
}

.frame-grey {
  border: #a8a8a8 3px solid;
  border-radius: 5px;
}

.block-message {
  border-radius: 25px;
  border: #a8a8a8 3px solid;
  background: rgba(255, 255, 255, 0.9);
}

.rectangle-slider {
  width: 100%;
  height: 100%;
  background: #fffdf9;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
}

.rectangle-slider__img {
  opacity: 0;
  position: absolute;
}

.rectangle-slider__img:first-child {
  opacity: 1;
}

.portfolio {
  background: #fffdf9;
  padding: 45px 0;
}

.portfolio__items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  padding-top: 45px;
  justify-content: center;
}

.portfolio__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(41.66666666666667% - 30px);
  margin-bottom: 15px;
}

.portfolio__link {
  display: block;
}

.portfolio-title {
  color: #cd5c5c;
}

.title {
  font-size: 1.8em;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
}

.contacts {
  color: #fffdf9;
}

.contacts-title {
  font-size: 1.5em;
  padding-bottom: 10px;
}

.footer {
  margin-top: auto;
  background: #cd5c5c;
  padding: 40px 0;
  color: #fffdf9;
}

.footer__flex {
  flex-basis: 100%;
  display: flex;
}

.footer__nav {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  display: flex;
  margin-left: auto;
}

.email {
  font-weight: 600;
}

.email__link {
  color: #00ff73;
  text-decoration: none;
  font-weight: 400;
}

.btn-up {
  width: 50px;
  position: fixed;
  top: 90%;
  opacity: 0.8;
  display: none;
  cursor: pointer;
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .header-top {
    flex-direction: column;
    align-items: center;
  }

  .header-top__logo {
    padding-top: 25px;
  }

  .header-top__nav {
    margin: 50px auto 0 auto;
  }

  .main-title {
    margin-top: 50px;
    flex-direction: column;
    align-items: center;
  }

  .main-title__text {
    width: calc(100% - 30px);
  }

  .main-title__text {
    margin: 25px 0;
  }

  .main-title__slider {
    width: calc(100% - 30px);
  }

  .main-title__slider {
    margin-bottom: 25px;
  }

  .footer__flex {
    flex-direction: column;
    align-items: center;
  }

  .footer__nav {
    margin: 50px auto 0 auto;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .nav__ul {
    flex-direction: column;
  }

  .portfolio__item {
    width: calc(100% - 30px);
  }
}